  border-radius: 2px;
  position: absolute;
  left: 0;
  top: 100%;
  opacity: 0;
  margin-top: 6px;
  pointer-events: none;
  transition: .3s;
}

.sub_menu:before {
  content: '';
  background: transparent;
  width: 100%;
  height: 6px;
  left: 0;
  top: -6px;
  position: absolute;
}

